<template>
  <el-card class="code_wrapper">
    <el-row :gutter="20">
      <el-col :span="5" :offset="8">
        <p class="code_title">{{ $t("code.codeTitle") }}</p>
      </el-col>
      <el-col :span="3" :offset="5">
        <el-button class="add_btn" type="text" @click="addCode" autofocus icon="add_btn el-icon-circle-plus-outline"></el-button>
      </el-col>
      <el-col :span="3"> <el-button class="minis_btn" type="text" @click="minisCode" icon="el-icon-remove-outline"></el-button></el-col>
    </el-row>
    <number-btn :number="tableCount"></number-btn>
  </el-card>
</template>

<script>
import NumberBtn from "@/views/code/components/NumberBtn";
import { alterTableNumber } from "@/api/codeAPI";
import { mapMutations, mapState } from "vuex";
export default {
  methods: {
    async addCode() {
      // 添加桌号
      let params = {
        operate_mode: "add",
        shop_id: this.shopId,
      };
      let ret = await alterTableNumber(params);
      if (ret.status === 200) {
        this.addTableCount();
      }
    },
    async minisCode() {
      if (this.tableCount.length === 1) return;
      let params = {
        operate_mode: "minus",
        shop_id: this.shopId,
      };
      let ret = await alterTableNumber(params);
      if (ret.status === 200) {
        this.minisTableCount();
      }
    },
    ...mapMutations({
      addTableCount: "user/ADD_TABLE_COUNT",
      minisTableCount: "user/MINIS_TABLE_COUNT",
    }),
  },
  computed: {
    ...mapState({
      shopId: (state) => state.user.data.role_id,
      tableCount: (state) => state.user.tableCount,
    }),
  },
  components: {
    NumberBtn,
  },
};
</script>

<style lang="scss" scoped>
.code_wrapper {
  height: calc(100vh - 8em);
  overflow-y: auto;
}
.code_title {
  background-color: $--color-primary;
  text-align: center;
  padding: 1em;
  color: #fff;
  border-radius: 0.5em;
}
.add_btn,
.minis_btn {
  font-size: 30px;
}
.btn_wrapper {
  display: inline-block;
  .number_btn {
    width: 50px;
    height: 50px;
    border-radius: 1em;
  }
}
</style>
