<template>
  <div class="dialog_wrapper">
    <div class="code_title">
      {{ title }}<span class="table_number">{{ number }}</span>
    </div>
    <div class="code_img">
      <canvas id="canvas"></canvas>
    </div>
  </div>
</template>

<script>
import QRCode from "qrcode";
export default {
  mounted() {
    const canvas = document.getElementById("canvas");
    QRCode.toCanvas(canvas, this.result, function (err) {
      if (err) {
        this.$message.error(this.$t("error.serverErr"));
      }
    });
  },
  props: ["result", "title", "number"],
};
</script>

<style lang="scss" scoped>
.code_title,
.code_img {
  text-align: center;
  padding-top: 1em;
}
.table_number {
  font-weight: bold;
  color: $--color-primary;
  padding-left: 1em;
}
</style>
