<template>
  <div>
    <div class="btn_wrapper" v-for="item in number" :key="item.id">
      <el-button
        v-loading="item.loading"
        class="number_btn"
        type="primary"
        plain
        @click="click(item)"
        >{{ item.id }}</el-button
      >
      <code-dialog v-if="false"></code-dialog>
    </div>
  </div>
</template>

<script>
import CodeDialog from "./CodeDialog.vue";
import { getCodeInformation } from "@/api/codeAPI";
import { mapMutations, mapState } from "vuex";
export default {
  created() {
    console.log(this.number);
  },
  props: ["number"],

  methods: {
    async click(item) {
      this.alterLoading({ id: item.id, loading: true });
      const obj = {
        shop_id: this.shopId,
        table_id: item.id,
      };
      let ret = await getCodeInformation(obj);
      if (ret.status === 200) {
        this.alterLoading({ id: item.id, loading: false });
        let result = ret.data.QRcode_txt;
        const h = this.$createElement;
        this.$msgbox({
          message: h(CodeDialog, {
            props: {
              result,
              title: this.$t("code.codeTitle"),
              number: item.id,
            },
          }),
          confirmButtonText: this.$t("btn.confirm"),
        });
      } else {
        //   请求失败
        this.alterLoading({ id: item.id, loading: false });
        this.$message.error(this.$t("error.serverErr"));
      }
    },
    ...mapMutations({
      alterLoading: "user/ALTER_TABLE_LOADING",
    }),
  },
  computed: {
    ...mapState({
      shopId: (state) => state.user.data.role_id,
    }),
  },
  components: {
    CodeDialog,
  },
};
</script>

<style lang="scss" scoped>
.btn_wrapper {
  display: inline-block;
  padding: 1em;
  .number_btn {
    width: 60px;
    height: 60px;
    border-radius: 20px;
    font-weight: bold;
    font-size: 16px;
  }
}
.loading_wrapper {
  height: 200px;
}
</style>
