import $axios from './http'
import {
    parseObjectToGetUrl
} from '@/util'

export function getCodeInformation(params) {
    const url = 'get_shop_QRcode'
    params = parseObjectToGetUrl(params)
    return $axios.get(url + params)
}

// 操作桌号
export function alterTableNumber(params) {
    const url = 'operate_tablecount'
    return $axios.post(url, params)
}